.ttmap-info-box {
    padding: 6px 8px;
    font: 14px/16px Arial, Helvetica, sans-serif;
    background: white;
    background: rgba(255,255,255,0.8);
    box-shadow: 0 0 15px rgba(0,0,0,0.2);
    border-radius: 5px;
}
.info h4 {
    margin: 0 0 5px;
    color: #777;
}
.leaflet-container.crosshair-cursor-enabled {
    cursor:crosshair;
}
/* Needed for control.hide() in calculateRealworldRoute() to hide route instructions */
/* See https://github.com/perliedman/leaflet-routing-machine/issues/20 for more info */
.leaflet-control-container .leaflet-routing-container-hide {
    display: none;
}

 /* Set the zIndex (590) slightly below markers (600) to prevent them cover up MarkerClusters yet still be above other items as tooltips would normally be */
.leaflet-distanceTooltip-pane {
    z-index: 590;
}